import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PricingComponent } from './pricing.component';

const routes: Routes = [{ path: '', component: PricingComponent, data: { title: 'J U P I T A | Pricing For Any Size Enterprise', metaTitle: 'Pricing For Any Size Enterprise', metaDescription: 'Only pay for what you use - no contracts or subscriptions, ever. Pay-as-you-go gives you the freedom to scale up or down anytime.' } }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PricingRoutingModule { }
